import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { NavLink } from "react-router-dom";
import "./styles.scss";

const Breadcrumbs = () => {
  const {
    orderId,
    selectedCity,
    selectedPoint,
    selectedCar,
    selectedColor,
    startDateRate,
    endDateRate,
    selectedRate,
  } = useSelector((state) => state.order);
  const className = "breadcrumbs__item";
  const isActiveBreadcrumbs = ({ isActive }) =>
    isActive ? className + " breadcrumbs__item_active" : className;

  const [stepsBreadcrumbs, setStepsBreadcrumbs] = useState([
    {
      id: "step1",
      name: "",
      linkTo: "/order/point",
      disabled: false,
    },
    { id: "step2", name: "", linkTo: "/order/model", disabled: true },
    {
      id: "step3",
      name: "",
      linkTo: "/order/additionally",
      disabled: true,
    },
    { id: "step4", name: "", linkTo: "/order/total", disabled: true },
  ]);

  useEffect(() => {
    if (selectedCity && selectedPoint) {
      const newStepData = stepsBreadcrumbs.map((step) => {
        step.id === "step2" ? (step.disabled = false) : null;
        return step;
      });
      setStepsBreadcrumbs(newStepData);
    } else {
      const newStepData = stepsBreadcrumbs.map((step) => {
        step.id === "step2" ? (step.disabled = true) : null;
        return step;
      });
      setStepsBreadcrumbs(newStepData);
    }
  }, [selectedCity, selectedPoint]);

  useEffect(() => {
    if (selectedCar) {
      const newStepData = stepsBreadcrumbs.map((step) => {
        step.id === "step3" ? (step.disabled = false) : null;
        return step;
      });
      setStepsBreadcrumbs(newStepData);
    } else {
      const newStepData = stepsBreadcrumbs.map((step) => {
        step.id === "step3" ? (step.disabled = true) : null;
        return step;
      });
      setStepsBreadcrumbs(newStepData);
    }
  }, [selectedCar]);

  useEffect(() => {
    if (selectedColor && startDateRate && endDateRate && selectedRate) {
      const newStepData = stepsBreadcrumbs.map((step) => {
        step.id === "step4" ? (step.disabled = false) : null;
        return step;
      });
      setStepsBreadcrumbs(newStepData);
    } else {
      const newStepData = stepsBreadcrumbs.map((step) => {
        step.id === "step4" ? (step.disabled = true) : null;
        return step;
      });
      setStepsBreadcrumbs(newStepData);
    }
  }, [selectedColor, startDateRate, endDateRate, selectedRate]);

  return (
    <div className="breadcrumbs__list main-wrapper">
      {orderId ? (
        <div className="breadcrumbs__item">
            {orderId.toUpperCase()}
        </div>
      ) : (
        stepsBreadcrumbs.map((step) => {
          const stepClassName = step.disabled
            ? className + " breadcrumbs__item_disabled"
            : isActiveBreadcrumbs;
          return (
            <NavLink to={step.linkTo} className={stepClassName} key={step.id}>
              {step.name}
            </NavLink>
          );
        })
      )}
    </div>
  );
};

export default Breadcrumbs;
