import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link, useLocation, useNavigate, useParams } from "react-router-dom";
import { setOrder, setTotalPrice } from "../../../redux/orderSlice";
import "./styles.scss";

const OrderItem = ({ name, value }) => {
  return (
    <div className="order__item">
      <div className="order__name-item">{name}</div>
      <div className="order__dotted" />
      <div className="order__value-item">{value}</div>
    </div>
  );
};

const OrderDetails = () => {
  const dispatch = useDispatch();
  const {
    orderId,
    selectedCity,
    selectedPoint,
    selectedCar,
    selectedColor,
    startDateRate,
    endDateRate,
    selectedRate,
    isFullTank,
    isNeedChildChair,
    isRightWheel,
    totalPrice,
  } = useSelector((state) => state.order);
  const { stepOrder } = useParams();
  const [buttonAction, setButtonAction] = useState({
    name: "",
    linkTo: "link",
  });
  const [buttonDisabled, setButtonDisabled] = useState(true);
  const [displayConfirmOrder, setDisplayConfirmOrder] = useState(false);
  const [countTimeRate, setCountTimeRate] = useState(null);
  const [price, setPrice] = useState(totalPrice);
  const navigate = useNavigate();
  const location = useLocation();

  useEffect(() => {
    if (orderId && location.pathname === "/order/total") {
      navigate(orderId);
    }
  }, [orderId]);

  useEffect(() => {
    if (selectedCity && selectedPoint && stepOrder === "point")
      setButtonDisabled(false);
    else setButtonDisabled(true);
  }, [selectedCity, selectedPoint]);

  const setTotalPriceRate = () => {
    if (startDateRate && endDateRate) {
      let timeText = "";
      let minutes = Math.ceil(
        Math.abs(endDateRate - startDateRate) / (1000 * 60)
      );
      let hours = Math.floor(minutes / 60);
      const days = Math.floor(hours / 24);

      if (selectedRate) {
        let amountMoney = selectedCar.priceMin;
        switch (selectedRate.rateTypeId.unit) {
          case "":
            amountMoney += minutes * selectedRate.price;
            break;
          case "":
            amountMoney += Math.ceil(minutes / (60 * 24)) * selectedRate.price;
            break;
          case "7 ":
            amountMoney +=
              Math.ceil(minutes / (60 * 24 * 7)) * selectedRate.price;
            break;
          case "30 ":
            amountMoney +=
              Math.ceil(minutes / (60 * 24 * 30)) * selectedRate.price;
            break;
          case "90 ":
            amountMoney +=
              Math.ceil(minutes / (60 * 24 * 90)) * selectedRate.price;
            break;
          case "365 ":
            amountMoney +=
              Math.ceil(minutes / (60 * 24 * 365)) * selectedRate.price;
            break;
        }
        if (isFullTank.value) amountMoney += isFullTank.price;
        if (isNeedChildChair.value) amountMoney += isNeedChildChair.price;
        if (isRightWheel.value) amountMoney += isRightWheel.price;
        dispatch(setTotalPrice(amountMoney));
      } else {
        dispatch(setTotalPrice(null));
      }

      hours = hours - days * 24;
      minutes = minutes - days * 24 * 60 - hours * 60;
      if (days !== 0) timeText += days + " ";
      if (hours !== 0) timeText += hours + " ";
      if (minutes !== 0) timeText += minutes + "";
      setCountTimeRate(timeText);
    } else {
      setCountTimeRate(null);
    }
  };

  useEffect(() => {
    setTotalPriceRate();
  }, [
    startDateRate,
    endDateRate,
    selectedRate,
    isFullTank,
    isNeedChildChair,
    isRightWheel,
  ]);

  useEffect(() => {
    if (
      selectedColor &&
      startDateRate &&
      endDateRate &&
      selectedRate &&
      stepOrder === "additionally"
    )
      setButtonDisabled(false);
    else setButtonDisabled(true);
  }, [selectedColor, startDateRate, endDateRate, selectedRate]);

  useEffect(() => {
    if (selectedCar && stepOrder === "model") setButtonDisabled(false);
    else setButtonDisabled(true);
  }, [selectedCar]);

  useEffect(() => {
    setPrice(totalPrice);
  }, [totalPrice]);

  useEffect(() => {
    switch (stepOrder) {
      case "point":
        setButtonAction({
          name: " ",
          linkTo: "/order/model",
        });
        if (selectedCity && selectedPoint) setButtonDisabled(false);
        else setButtonDisabled(true);
        break;
      case "model":
        setButtonAction({
          name: "",
          linkTo: "/order/additionally",
        });
        if (selectedCar) setButtonDisabled(false);
        else setButtonDisabled(true);
        break;
      case "additionally":
        setButtonAction({
          name: "",
          linkTo: "/order/total",
        });
        if (selectedColor && startDateRate && endDateRate && selectedRate)
          setButtonDisabled(false);
        else setButtonDisabled(true);
        break;
      case "total":
        setButtonAction({
          name: "",
          linkTo: "#",
          onClick: handleOrderClick,
        });
        break;
      default: {
        const regex = new RegExp("order/total/", "g");
        if (location.pathname.match(regex)) {
          setButtonAction({
            name: "",
            linkTo: "#",
            onClick: handleConfirmOrderClick,
            styleButton: "button_orange",
          });
        }
      }
    }
  }, [stepOrder]);

  const handleOrderClick = () => {
    setDisplayConfirmOrder(!displayConfirmOrder);
  };

  const handleConfirmOrderClick = () => {
    if (!orderId) {
      dispatch(setOrder());
    }
    setDisplayConfirmOrder(false);
  };

  return (
    <div className="content__order order">
      {selectedCity && selectedPoint && (
        <>
          <div className="order__header"> :</div>
          <OrderItem
            name={" "}
            value={selectedCity.name + ", " + selectedPoint.address}
          />

          {selectedCar && (
            <OrderItem name={""} value={selectedCar.name} />
          )}
          {selectedColor && <OrderItem name={""} value={selectedColor} />}
          {countTimeRate && (
            <OrderItem name={" "} value={countTimeRate} />
          )}
          {selectedRate && (
            <OrderItem name={""} value={selectedRate.rateTypeId.name} />
          )}
          {isFullTank.value && (
            <OrderItem name={isFullTank.name} value={""} />
          )}
          {isNeedChildChair.value && (
            <OrderItem name={isNeedChildChair.name} value={""} />
          )}
          {isRightWheel.value && (
            <OrderItem name={isRightWheel.name} value={""} />
          )}

          {selectedCar && !price ? (
            <div className="order__price">
              <span className="order__price-name">: </span>
              <span className="order__amount-money">
                 {selectedCar.priceMin}  {selectedCar.priceMax} 
              </span>
            </div>
          ) : price ? (
            <div className="order__price">
              <span className="order__price-name">: </span>
              <span className="order__amount-money">{price}</span>
            </div>
          ) : null}
        </>
      )}

      <Link
        to={buttonAction.linkTo}
        className={
          buttonDisabled
            ? "order__action button disabled"
            : buttonAction?.styleButton
            ? "order__action button " + buttonAction.styleButton
            : "order__action button"
        }
        onClick={buttonAction.onClick}
      >
        {buttonAction.name}
      </Link>

      {displayConfirmOrder && (
        <div className="confirm-order">
          <div className="confirm-order__label">
            {orderId ? " " : " "}
          </div>
          <div className="confirm-order__actions">
            <button className="button" onClick={handleConfirmOrderClick}>
              
            </button>
            <button className="button button_orange" onClick={handleOrderClick}>
              
            </button>
          </div>
        </div>
      )}
    </div>
  );
};

export default OrderDetails;
