import React, { useEffect, useState } from "react";
import "./styles.scss";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import { useDispatch, useSelector } from "react-redux";
import {
  setStartDateRate,
  setEndDateRate,
  setFullTank,
  setNeedChildChair,
  setRightWheel,
  setSelectedColor,
  setSelectedRate,
  getRates,
} from "../../../redux/orderSlice";

const AdditionallySelection = () => {
  const dispatch = useDispatch();
  const {
    ratesData,
    selectedCar,
    selectedColor,
    startDateRate,
    endDateRate,
    selectedRate,
    isFullTank,
    isNeedChildChair,
    isRightWheel,
  } = useSelector((state) => state.order);
  const [selectColor, setSelectColor] = useState(null);
  const [dateFrom, setDateFrom] = useState(null);
  const [dateTo, setDateTo] = useState(null);
  const [selectRate, setSelectRate] = useState(null);
  const [rates, setRates] = useState(ratesData ?? null);
  const [additionallyServiceData, setAdditionallyServiceData] = useState([
    isFullTank,
    isNeedChildChair,
    isRightWheel,
  ]);
  const colors = selectedCar ? ["", ...selectedCar.colors] : null;
  const currentDate = new Date();

  useEffect(() => {
    dispatch(getRates());
  }, []);

  useEffect(() => {
    setRates(ratesData);
  }, [ratesData]);

  useEffect(() => {
    setSelectColor(selectedColor);
  }, [selectedColor]);

  useEffect(() => {
    setDateFrom(startDateRate ? new Date(startDateRate) : null);
    setDateTo(endDateRate ? new Date(endDateRate) : null);
  }, [startDateRate, endDateRate]);

  useEffect(() => {
    setSelectRate(selectedRate);
  }, [selectedRate]);

  useEffect(() => {
    setAdditionallyServiceData([isFullTank, isNeedChildChair, isRightWheel]);
  }, [isFullTank, isNeedChildChair, isRightWheel]);

  const filterPassedStartTime = (time) => {
    const selectedDate = new Date(time);
    if (dateTo) {
      return (
        currentDate.getTime() < selectedDate.getTime() &&
        selectedDate.getTime() < dateTo.getTime()
      );
    }
    return currentDate.getTime() < selectedDate.getTime();
  };

  const filterPassedEndTime = (time) => {
    const selectedDate = new Date(time);
    if (dateFrom) {
      return (
        dateFrom.getTime() < selectedDate.getTime() &&
        selectedDate.getTime() > dateFrom.getTime()
      );
    }
    return currentDate.getTime() < selectedDate.getTime();
  };

  const handleChangeAddService = (item) => {
    switch (item.name) {
      case isFullTank.name:
        dispatch(setFullTank(!item.value));
        break;
      case isNeedChildChair.name:
        dispatch(setNeedChildChair(!item.value));
        break;
      case isRightWheel.name:
        dispatch(setRightWheel(!item.value));
        break;
    }
  };

  return (
    <>
      <div className="color-car">
        <label className="label"></label>
        <ul className="radio-list">
          {colors?.map((color, index) => (
            <li className="radio-list__item" key={index}>
              <input
                type="radio"
                className="visually-hidden radiobutton"
                name="color"
                value={color}
                id={color}
                onChange={() => dispatch(setSelectedColor(color))}
                checked={selectColor === color && true}
              />
              <label htmlFor={color}>{color}</label>
            </li>
          ))}
        </ul>
      </div>
      <div className="rental">
        <label className="label"> </label>
        <div className="rental__dates">
          <div className="rental__date-from">
            <label htmlFor="dateFrom" className="label">
              
            </label>
            <DatePicker
              lassName={"datepicker"}
              selected={dateFrom}
              onChange={(date) =>
                dispatch(setStartDateRate(date?.getTime() ?? null))
              }
              placeholderText="   "
              dateFormat="dd.MM.yyyy HH:mm"
              minDate={new Date()}
              maxDate={dateTo}
              timeFormat="HH:mm"
              filterTime={filterPassedStartTime}
              showTimeSelect
              timeIntervals={1}
              isClearable
            />
          </div>
          <div className="rental__date-to">
            <label htmlFor="dateTo" className="label">
              
            </label>
            <DatePicker
              lassName={"datepicker"}
              selected={dateTo}
              onChange={(date) =>
                dispatch(setEndDateRate(date?.getTime() ?? null))
              }
              placeholderText="   "
              dateFormat="dd.MM.yyyy HH:mm"
              minDate={dateFrom}
              timeFormat="HH:mm"
              filterTime={filterPassedEndTime}
              showTimeSelect
              timeIntervals={1}
              isClearable
            />
          </div>
        </div>
      </div>
      <div className="rate">
        <label className="label"></label>
        <ul className="radio-list">
          {rates.map((rate) => (
            <li className="radio-list__item" key={rate.id}>
              <input
                type="radio"
                className="visually-hidden radiobutton"
                name="rate"
                value=""
                id={rate.id}
                onChange={() => dispatch(setSelectedRate(rate))}
                checked={selectRate?.id === rate.id && true}
              />
              <label htmlFor={rate.id}>
                {rate.rateTypeId.name}, {rate.price}/{rate.rateTypeId.unit}
              </label>
            </li>
          ))}
        </ul>
      </div>
      <div className="add-service">
        <label className="label"> </label>
        <ul className="checkbox-list">
          {additionallyServiceData.map((item, index) => (
            <li className="checkbox-list__item" key={index}>
              <input
                type="checkbox"
                className="visually-hidden checkbutton"
                id={item.name}
                onChange={() => handleChangeAddService(item)}
                checked={item.value}
              />
              <label htmlFor={item.name} className="label">
                {item.name}, {item.price}
              </label>
            </li>
          ))}
        </ul>
      </div>
    </>
  );
};

export default AdditionallySelection;
